	module.exports = {"data":{"markdownRemark":{"html":"<p><strong>本日の結果: 2,320円→3,491円 (+1,171円 +50% / 累積+16.3%)</strong> <em>や　っ　た　ぜ。</em> 3 日間隙間時間でちょっとお勉強して、その成果を出してみたら大成功っ！ただ、今日一日だけだとまぐれな可能性もあるので、もうちょっと成果を検証してみる必要はありですね。  ということで、お勉強しつつ、どういうときに幾ら買う(売る)、指値はどうするか、TP/SL設定はどうするかなどをきっちりルール化してみました。 ルールについては、本当に勝てるのかまだ検証できていないので、あなたが真似て自爆しないよう、じっくり検討してから公開したいと思います。 今回公開するのは入出金ルールのみで。</p>\n<ul>\n<li>増資(現金投入)は、毎月中旬を目途に行う。</li>\n<li>増資額は、負けている際は最低額 3,000 円。勝ち進んでいる際は、状況を見て臨機応変に。</li>\n<li>勝ち(ミニマムサクセス)と判定する基準: 月利 5 パーセント以上。</li>\n<li>55万円以上現金投入するまでは、原則出金しない。ロスカットして全部パァになっても泣かない。</li>\n</ul>","frontmatter":{"cover":null,"date":"2016-01-26T23:27:29+09:00","strDate":"2016/1/26","title":"[FX]反撃の狼煙を上げるぜ"}}},"pathContext":{"path":"/ja/blog/2016/01/26-fx反撃の狼煙を上げるぜ/","langKey":"ja"}}

